nav {
    background: rgba(0,0,0,0.3);
    width: max-content;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 1rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}

nav a{
    background: transparent;
    padding: 0.5rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
}
nav a:hover{
    background: rgba(0,0,0,0.4);

}

nav a.active {
    background: var(--color-primary);
    color: var(--color-bg);
}
@media screen and (max-width: 600px) {
    nav{
        margin: 0 auto;
        width: fit-content;
        padding: 0.7rem 1rem;
    }
    nav a{
        padding: 0.85rem;
    }
}